import React, { useState, useEffect } from 'react'
import Spinner from 'react-bootstrap/Spinner'
import Jumbotron from 'react-bootstrap/Jumbotron'
import Button from 'react-bootstrap/Button'
import Container from 'react-bootstrap/Container'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'

import { withRouter } from 'react-router-dom'

import { fetchCourse,  deleteCourse } from '../../api'

function CoursesShowPage(props) {
  const [data, setData] = useState({});
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    fetchCourse(props.match.params.id).then((response)=>{
      console.log(response.data)
      setData(response.data.course)
      setIsLoading(false)
    })
  }, []);

 const handleEditCourse = (id) => {
  props.history.push({
   pathname: `/courses/${id}/edit`
  });
 };

 const handleDeleteCourse = (id) => {
  // const course = { prod_name: data.prod_name, prod_desc: data.prod_desc, prod_price: parseInt(data.prod_price) };
  deleteCourse(id)
   .then((result) => {
    setIsLoading(false);
    props.history.push('/courses')
   }).catch((error) => setIsLoading(false));
 };

 return (
    <Container style={{marginTop:'2em'}}>
      <Row>
        <Col>
   {((isLoading) && (<Spinner animation="border" role="status">
    <span className="sr-only">Loading...</span>
   </Spinner>)) || (
   <Jumbotron>
    <h2>{data.title}</h2>
    <p>{data.description}</p>
    <p>
     <Button type="button" variant="primary" onClick={() => { handleEditCourse(data.id) }}>Edit</Button>&nbsp;
     <Button type="button" variant="danger" onClick={() => { handleDeleteCourse(data.id) }}>Delete</Button>
    </p>
   </Jumbotron>)}
  </Col>
  </Row>
  </Container>
 );
}

export default withRouter(CoursesShowPage);